import { Form, DatePicker } from "antd";
import dayjs from "dayjs";
import React from "react";

const CDatePicker = ({
  label,
  validate,
  name,
  isRequired,
  isDisabled,
  onChange = () => {},
  value,
  id,
  addonBefore,
  ...rest
}) => {
  const dateFormat = "YYYY-MM-DD";
  const dates = value ? dayjs(value, dateFormat) : "";

  return (
    <Form.Item
      label={
        <span>
          {label}
          {isRequired ? (
            <span style={{ marginLeft: "5px", color: "red" }}>*</span>
          ) : null}
        </span>
      }
      name={name}
      {...rest}
      validateStatus={validate ? "error" : null}
      help={validate}
    >
      <DatePicker
        onChange={onChange}
        defaultValue={dates}
        format={dateFormat}
        placeholder={label}
        disabled={isDisabled}
        value={dates}
        autoClose
      />
    </Form.Item>
  );
};

export default CDatePicker;
